import React, { useMemo } from 'react';
import { Heart } from 'lucide-react';
import FloatingHeartsBackground from './FloatingHeartsBackground';

const GameScreen = ({ 
  gameState, 
  fallingTiles, 
  powerUps, 
  powerUpTypes, 
  activePowerUps,
  onTileClick, 
  onPowerUpCollect, 
  onReset 
}) => {
  // Generate stable heart positions that don't change on re-render
  const heartPositions = useMemo(() => 
    [...Array(12)].map(() => ({
      left: Math.random() * 100,
      top: Math.random() * 100,
      animationDelay: Math.random() * 8,
      pulseDelay: Math.random() * 5
    })), []
  );

  return (
    <div className="min-h-screen dynamic-bg relative overflow-hidden flex items-center justify-center">
      {/* Floating Hearts Background Animation */}
      <FloatingHeartsBackground 
        heartCount={12} 
        opacity={0.2} 
        animationDuration={8}
        pulseDuration={10}
        size="w-5 h-5"
        color="text-pink-200"
      />

      {/* Game Container */}
      <div className="glass-effect-dark rounded-3xl shadow-2xl p-6 w-96 h-[600px] relative overflow-hidden">
        {/* Game UI Header */}
        <div className="flex justify-between items-center text-white mb-2">
          <div className="glass-effect rounded-xl px-3 py-1">
            <p className="text-xs font-semibold">Score: {gameState.score}</p>
          </div>
          <div className="glass-effect rounded-xl px-3 py-1">
            <p className="text-xs font-semibold">‚ù§Ô∏è {gameState.lives}</p>
          </div>
          <div className="glass-effect rounded-xl px-3 py-1">
            <p className="text-xs font-semibold">Hit: {gameState.tilesHit}/{gameState.endlessMode ? '‚àû' : 12}</p>
          </div>
          {gameState.endlessMode && (
            <button
              onClick={onReset}
              className="glass-effect rounded-xl px-3 py-1 text-xs font-semibold hover:scale-105 transition-all duration-300"
            >
              üö™ Exit
            </button>
          )}
        </div>

        {/* Combo and Power-up Status */}
        <div className="flex justify-between items-center text-white mb-2">
          <div className="glass-effect rounded-xl px-3 py-1">
            <p className="text-xs font-semibold">üî• Combo: {gameState.combo}</p>
          </div>
          <div className="glass-effect rounded-xl px-3 py-1">
            <p className="text-xs font-semibold">üèÜ Best: {gameState.maxCombo}</p>
          </div>
        </div>

        {/* Active Power-ups Display */}
        {(activePowerUps.invincible.active || activePowerUps.slowMotion.active || activePowerUps.scoreMultiplier.active) && (
          <div className="flex justify-center space-x-2 mb-2">
            {activePowerUps.invincible.active && (
              <div className="glass-effect rounded-full px-2 py-1 text-xs font-bold text-yellow-300 animate-pulse">
                ‚≠ê {Math.ceil(activePowerUps.invincible.timeLeft / 1000)}s
              </div>
            )}
            {activePowerUps.slowMotion.active && (
              <div className="glass-effect rounded-full px-2 py-1 text-xs font-bold text-blue-300 animate-pulse">
                ‚ö° {Math.ceil(activePowerUps.slowMotion.timeLeft / 1000)}s
              </div>
            )}
            {activePowerUps.scoreMultiplier.active && (
              <div className="glass-effect rounded-full px-2 py-1 text-xs font-bold text-purple-300 animate-pulse">
                üåà {activePowerUps.scoreMultiplier.multiplier}x ({Math.ceil(activePowerUps.scoreMultiplier.timeLeft / 1000)}s)
              </div>
            )}
          </div>
        )}

        {/* Game Area with 4 columns */}
        <div className={`relative h-[500px] overflow-hidden rounded-2xl bg-gradient-to-b from-indigo-900/50 via-purple-900/50 to-pink-900/50 ${activePowerUps.invincible.active ? 'ring-4 ring-yellow-400 ring-opacity-60' : ''} ${activePowerUps.slowMotion.active ? 'ring-4 ring-blue-400 ring-opacity-60' : ''}`}>
          <div className="h-full grid grid-cols-4 gap-1">
            {[0, 1, 2, 3].map(column => (
              <div key={column} className="relative bg-white/5 border-l border-r border-white/10">
                {/* Column background lines */}
                <div className="absolute inset-0 opacity-20">
                  {[...Array(8)].map((_, i) => (
                    <div key={i} className="h-16 border-b border-white/10"></div>
                  ))}
                </div>
              </div>
            ))}
          </div>

          {/* Falling Tiles */}
          {fallingTiles.map(tile => (
            <div
              key={tile.id}
              className="absolute z-20"
              style={{
                left: `${(tile.column * 25) + 0.5}%`,
                top: `${tile.y}px`,
                width: '23%',
                height: '60px'
              }}
            >
              <button
                onClick={() => onTileClick(tile.id)}
                onMouseDown={() => onTileClick(tile.id)}
                onTouchStart={(e) => {
                  e.preventDefault();
                  onTileClick(tile.id);
                }}
                className="w-full h-full bg-gradient-to-br from-yellow-400 to-orange-500 text-white font-bold text-lg rounded-lg shadow-lg hover:scale-105 active:scale-95 transform transition-all duration-75 cursor-pointer select-none border-2 border-yellow-300"
                style={{
                  touchAction: 'manipulation',
                  userSelect: 'none',
                  WebkitTapHighlightColor: 'transparent'
                }}
              >
                {tile.note.note}
                <div className="text-xs opacity-80">üéµ</div>
              </button>
            </div>
          ))}

          {/* Power-ups */}
          {powerUps.map(powerUp => (
            <div
              key={powerUp.id}
              className="absolute z-20"
              style={{
                left: `${(powerUp.column * 25) + 0.5}%`,
                top: `${powerUp.y}px`,
                width: '23%',
                height: '60px'
              }}
            >
              <button
                onClick={() => onPowerUpCollect(powerUp.id, powerUp.type)}
                onMouseDown={() => onPowerUpCollect(powerUp.id, powerUp.type)}
                onTouchStart={(e) => {
                  e.preventDefault();
                  onPowerUpCollect(powerUp.id, powerUp.type);
                }}
                className={`w-full h-full bg-gradient-to-br ${powerUpTypes[powerUp.type].color} text-white font-bold text-2xl rounded-lg shadow-lg hover:scale-110 active:scale-95 transform transition-all duration-75 cursor-pointer select-none border-2 border-white/50 animate-pulse`}
                style={{
                  touchAction: 'manipulation',
                  userSelect: 'none',
                  WebkitTapHighlightColor: 'transparent'
                }}
                title={powerUpTypes[powerUp.type].effect}
              >
                {powerUpTypes[powerUp.type].emoji}
              </button>
            </div>
          ))}

          {/* Bottom danger zone indicator */}
          <div className="absolute bottom-0 left-0 right-0 h-16 bg-gradient-to-t from-red-500/40 to-transparent pointer-events-none">
            <div className="absolute bottom-1 left-1/2 transform -translate-x-1/2 text-white/70 text-xs">
              ‚ö†Ô∏è Danger Zone ‚ö†Ô∏è
            </div>
          </div>
        </div>

        {/* Game completed message */}
        {gameState.gameCompleted && (
          <div className="absolute inset-0 flex items-center justify-center z-30 bg-black/20 backdrop-blur-sm rounded-3xl">
            <div className="bg-gradient-to-r from-green-400 to-emerald-500 text-white px-6 py-3 rounded-2xl text-lg font-bold animate-bounce shadow-2xl text-center">
              <div className="mb-2">üéâ Perfect! üéâ</div>
              <button 
                onClick={() => {/* Handle celebration */}}
                className="bg-white/20 px-4 py-2 rounded-full text-sm hover:bg-white/30 transition-all"
              >
                See Your Surprise! üíù
              </button>
            </div>
          </div>
        )}

        {/* Reset button */}
        <div className="absolute bottom-4 right-4">
          <button
            onClick={onReset}
            className="glass-effect text-white px-3 py-2 rounded-full hover:scale-105 transition-all duration-300 text-sm"
          >
            üîÑ Reset
          </button>
        </div>
      </div>
    </div>
  );
};

export default GameScreen;
